.History {
  width: 100%;
  height: calc(100vh);
  background-color: #f0f1f8;
  position: relative;
  display: flex;
  z-index: 101;
  /* border-radius: 40px; */
  /* margin-top: 33px; */
}
.backgroundHistorySvg {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  /* top: 25%; */
  top: 0%;
  padding-left: 10%;
}
.backgroundHistorySvg svg {
  margin-top: 20%;
}
.History_block {
  position: relative;
  padding-top: 121px;
  /* padding-left: 82px; */
  width: 91%;
  height: 100%;
  z-index: 3;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
}
.History_block .hiddenOverflow {
  position: relative;
  width: 100%;
  top: 20%;
  height: 80%;
  overflow: hidden;
}

.History h2 {
  font-family: "Raleway-Light";
  font-style: normal;
  font-weight: 300;
  font-size: 33px;
  line-height: 75px;
  padding-left: 92px;
  text-transform: capitalize;
  /* margin-top: 44px; */
  position: absolute;

  background: linear-gradient(0deg, #2444a9, #2444a9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.textBlock {
  position: absolute;
  width: 60%;
  height: 100%;
  /* padding-top: 4%; */
  transition-delay: 0s;
  padding-left: 92px;
  /* transition: all 0.5s cubic-bezier(0,.11,.29,1); */
}
.textBlock_first {
  /* transform: translateY(0px); */
}
.textBlock_second {
  /* transform: translateY(880px); */
}
.textBlock_third {
  /* transform: translateY(1760px); */
}
.textBlock_text_content {
  position: relative;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  color: #102876;
  margin-top: 5%;
}
.textBlock_text {
  position: relative;
  font-family: "Raleway-Regular";
  font-style: normal;
  font-weight: 400;
  font-size: 88px;
  line-height: 100%;
  text-transform: capitalize;
  background: linear-gradient(
    98.83deg,
    #2444a9 8.79%,
    #526ec8 44.21%,
    #839ae9 79.62%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.textBlock_textPopinsMini {
  font-family: "Poppins-Light";
  font-style: normal;
  font-weight: 300;
  line-height: 100%;
  text-transform: capitalize;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}
.ImageBack img {
  width: 365px;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.TimeHistoryBox {
  position: absolute;
  right: 180px;
  top: 35%;
  width: auto;
  height: auto;
  border-right: 5px solid #4a6ad0;
  padding-right: 47px;
  overflow-y: scroll;
  z-index: 2;
  overflow: hidden;
  z-index: 1000;
}
.TimeHistoryBox_block {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-direction: column;
}
/* .TimeHistoryBox::-webkit-scrollbar {
    width: 0px;
} */
.TimeHistoryBox_element {
  font-family: "Poppins-Regular";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 33px;
  text-transform: uppercase;
  color: #4a6ad0;
  margin: 1.25px;
  transition: all 0.75s ease-in-out;
  cursor: pointer;
}
.TimeHistoryBox_element.active {
  color: #1839a3;
  /* transform: scale(2); */
  /* font-weight: 400; */
  font-size: 66px;
  line-height: 99px;
  /* text-transform: uppercase; */
}
.textBlock_text {
  position: relative;
  font-family: "Raleway-Regular";
  font-style: normal;
  font-weight: 400;
  font-size: 66px;
  line-height: 120%;
  text-transform: inherit;
  background: linear-gradient(
    98.83deg,
    #2444a9 8.79%,
    #526ec8 44.21%,
    #839ae9 79.62%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}
.textBlock_text_content {
  position: relative;
  font-style: normal;
  font-weight: 275;
  font-size: 22px;
  line-height: 26px;
  color: #102876;
  margin-top: 5%;
  width: 50%;
}
.ImageBack {
  position: absolute;
  width: 100%;
