  }
  .Wrapper_image .background_image {
    /* top: 60px; */
    height: calc(100vh - 60px);
  }
  .Wrapper_block_GEOTEC_title {
    font-size: 104px;
  }
  .Wrapper_block_GEOTEC_logo svg {
    width: 110px;
    height: 93px;
  }
  .Wrapper_block_GEOTEC_block1 {
    left: 15px;
  }
  .Wrapper_block_GEOTEC_block2 {
    left: 15px;
  }
  .Wrapper_block_GEOTEC_block3 {
    left: 15px;
    position: absolute;
    bottom: 20px;
    top: auto;
    height: auto;
    color: #ffffff;
  }
  .Wrapper_image {
    margin-top: 66px;
  }
  .Subtract_right {
    top: 66px;
  }
  .Subtract_left {
    top: 66px;
  }
  .Wrapper_block_GEOTEC_block2_button svg path {
    fill: #ffffff;
  }
  .Wrapper_block_GEOTEC_block2_button svg {
    width: 42px;
    height: 42px;
  }
  .Wrapper_block_GEOTEC_block2_button {
    left: 10px;
  }
}

