.App {
  position: relative;
  width: 100%;
  height: 100%;
  /* overflow: hidden; */
  /* overflow-y: scroll; */
  height: auto;
  /* scroll-behavior: smooth; */
  overflow: hidden;
}
.WrapperFixedContent {
  position: relative;
  height: auto;
  margin-top: 90vh;
}
.App::-webkit-scrollbar {
  width: 10px;
}
.App::-webkit-scrollbar-track {
  background-color: rgb(255, 255, 255, 1);
}
.App::-webkit-scrollbar-thumb {
  background-color: rgba(105, 165, 245, 0.8);
  border-radius: 20px;
  border: 2px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
}
.scrollbar-track-y {
  top: 0 !important;
  right: 0 !important;
  width: 10px !important;
  height: 100% !important;
  background-color: rgb(255, 255, 255, 1) !important;
}
.scrollbar-thumb {
  background-color: rgba(105, 165, 245, 0.8) !important;
  border-radius: 20px !important;
  border: 2px solid rgba(0, 0, 0, 0) !important;
  background-clip: padding-box !important;
}
.block_All {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 91%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  opacity: 0;
  z-index: 100;
}
.block_All_fixed {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 91%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  /* overflow: hidden; */
  /* opacity: 0; */
}
.borderRadius_overlow {
  position: relative;
  border-radius: 40px;
  /* overflow: hidden; */
  width: 100%;
  height: auto;
  /* overflow: scroll; */
}
.Subtract_left {
  position: fixed;
  top: 88px;
  left: 4.5%;
  width: 50px;
  z-index: 100;
}
.Subtract_right {
  position: fixed;
  top: 88px;
  right: 4.5%;
  width: 50px;
  z-index: 100;
  transform: scale(-1, 1);
}
.borderRadius_overlow_block {
  position: relative;
  width: 100%;
  height: auto;
}

